icos debe reservarse para casos en donde la cantidad de evacuaciones es elevada.")]),_c('p',[_vm._v("Siempre se aconseja que se acuda a supervisión médica cuando se está sufriendo de una diarrea, sin embargo, en algunos casos el asistir a un médico es una determinación obligatoria; por ejemplo en los casos de los niños menores de tres años, en las personas mayores a 65 años, en las mujeres embarazadas, en los pacientes con historial médico de enfermedades crónicas como el asma, úlcera y las enfermedades cardíacas; también si la diarrea es persistente y presenta un número elevado de evacuaciones con presencia de vómito y mal estado general.")])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row align-items-center",attrs:{"data-aos":"zoom-in"}},[_c('div',{staticClass:"col-auto mb-3"},[_c('h3',{staticClass:"mb-0 fnd-2-1 fnd-2"},[_vm._v("Estreñimiento")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('p',{staticClass:"mb-5"},[_c('b',[_vm._v("Prevención y recomendaciones:")]),_vm._v(" Para evitar que se sufra de estreñimiento, es recomendable tener un horario fijo para la defecación, hay que dedicar unos minutos diariamente a esta tarea. Más que tener una hora específica se trata de generar un ritmo de costumbre, por ejemplo, al momento de levantarse o después de ingerir el desayuno. Comer despacio, reposar la comida durante unos 15 o 20 minutos y el ejercicio físico disminuye los riesgos de sufrir estreñimiento.")])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center align-items-center mb-5",attrs:{"data-aos":"fade-right"}},[_c('div',{staticClass:"col-lg-10"},[_c('div',{staticClass:"nutrition-box nn"},[_c('div',{staticClass:"cont"},[_c('p',{staticClass:"mb-0"},[_vm._v("Si ya está sufriendo de estreñimiento, es recomendable evitar el tratamiento con fármacos, a no ser que sea com